html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100%;
}

section.top > .wr,
section.top > .wr header,
section.top > .wr header .logo,
section.top > .wr header nav ul,
section.snippets > .wr > ul,
section.portfolio > .wr,
section.team > .wr > .row2,
section.team > .wr > .row2 li,
footer {
  display: flex;
  flex-wrap: wrap;
}

section.top > .wr header nav ul,
section.snippets > .wr > ul,
section.team > .wr > .row2 {
  list-style: none;
  padding: 0;
  margin: 0;
}

.wr {
  max-width: 940px;
  margin: 0 auto;
  position: relative;
}

.red {
  color: #fa5c65;
}

.more {
  display: inline-block;
  margin-top: 30px;
  text-decoration: none;
  color: #fa5c65;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
}

/*-------------------------------
mixin
--------------------------------*/

section.top {
  background: url("../img/background.jpg") no-repeat center 0;
  background-size: cover;
  position: relative;
  height: 100vh;
  background-attachment: fixed;
}

section.top > .wr {
  height: 100%;
}

section.top > .wr header {
  flex-basis: 100%;
  justify-content: space-between;
  align-self: flex-start;
}

section.top > .wr header .logo {
  width: 140px;
  height: 74px;
  display: block;
  background-color: #fa5c65;
  color: #fff;
  border-radius: 0 0 10px 10px;
  text-align: center;
}

section.top > .wr header .logo h3 {
  font-size: 20px;
  margin-top: 40px;
}

section.top > .wr header .logo h3 span {
  margin-left: 5px;
  color: black;
}

section.top > .wr header nav ul {
  margin-top: 25px;
}

section.top > .wr header nav ul li {
  margin-left: 30px;
}

section.top > .wr header nav ul li a {
  color: #263742;
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-transform: uppercase;
  text-decoration: none;
  transition: all 0.3s ease;
}

section.top > .wr header nav ul li a:hover {
  color: #fa5c65;
}

section.top > .wr header nav ul li.social a {
  width: 25px;
  height: 25px;
  display: inline-block;
  font-size: 18px;
  color: #fa5c65;
  margin-left: 10px;
  -webkit-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  filter: grayscale(100%);
  transition: all 0.3s ease;
}

section.top > .wr header nav ul li.social a:hover {
  -webkit-filter: grayscale(0);
  -o-filter: grayscale(0);
  -moz-filter: grayscale(0);
  -ms-filter: grayscale(0);
  filter: grayscale(0);
}

section.top > .wr .sloganCont {
  max-width: 58%;
}

section.top > .wr .sloganCont h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 48px;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
  color: #314a5b;
}

section.top > .wr .sloganCont h4 {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  color: #314a5b;
  font-weight: 400;
  margin: 20px 0 35px 0;
}

section.top > .wr .sloganCont .btn {
  display: inline-block;
  text-decoration: none;
  margin-top: 45px;
}

section.top > .wr .sloganCont .btn span {
  color: #fff;
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  font-weight: 700;
  padding: 25px 35px;
  background-color: #fa5c65;
  border-radius: 10px;
  text-transform: uppercase;
}

section.snippets {
  position: relative;
}

section.snippets > .wr > ul {
  margin: 80px 0;
}

section.snippets > .wr > ul li {
  width: 33.3333333333%;
  box-sizing: border-box;
  padding-right: 55px;
}

section.snippets > .wr > ul li h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 26px;
  font-weight: 600;
  color: #465065;
  text-transform: uppercase;
}

section.snippets > .wr > ul li .descr {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  color: #9aa0ab;
}

section.snippets > .wr > ul li .more i {
  margin-left: 8px;
}

section.portfolio > .wr {
  justify-content: space-between;
}

section.portfolio > .wr > .col {
  width: 49%;
  margin-bottom: 30px;
}

section.portfolio > .wr > .col:first-of-type img {
  max-width: 100%;
}

section.portfolio > .wr > .col:nth-of-type(1) {
  align-self: center;
}

section.portfolio > .wr > .col h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 40px;
  color: #314a5b;
  text-transform: uppercase;
}

section.portfolio > .wr > .col h4 {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #314a5b;
  margin-bottom: 32px;
}

section.portfolio > .wr > .col .descr {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  color: #9aa0ab;
  margin-bottom: 32px;
}

section.team > .wr > .row1 h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 32px;
  font-weight: 700;
  text-transform: uppercase;
  color: #314a5b;
  margin: 0;
}

section.team > .wr > .row1 h3 {
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  color: #9aa0ab;
  font-weight: 400;
}

section.team > .wr > .row2 {
  margin-top: 30px;
}

section.team > .wr > .row2 li {
  width: 33.3333333333%;
  padding-right: 35px;
  box-sizing: border-box;
}

section.team > .wr > .row2 li .col1 {
  width: 128px;
  height: 128px;
  border-radius: 50%;
  background: no-repeat center / cover;
  background-image: url("../img/Joey.jpg");
}

section.team > .wr > .row2 li .col2 {
  width: calc(100% - 128px);
  padding-left: 20px;
  box-sizing: border-box;
}

section.team > .wr > .row2 li .col2 h3 {
  font-family: "Montserrat", sans-serif;
  color: #314a5b;
  font-size: 22px;
  font-weight: 700;
  text-transform: uppercase;
  margin: 0;
}

section.team > .wr > .row2 li .col2 h4 {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #fa5c65;
  margin: 0;
}

section.team > .wr > .row2 li .col2 .descr {
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  color: #9aa0ab;
}

footer {
  justify-content: space-between;
  align-items: center;
  min-height: 90px;
  border-top: 3px solid #e6eaed;
  margin-top: 80px;
}

footer .col1 i {
  display: inline-block;
  width: 21;
  height: 21px;
  margin-right: 21px;
  color: #b9c0c6;
}

footer .col2 {
  font-family: "Open Sans", sans-serif;
  font-size: 13px;
  color: #b9c0c6;
}